                <p class="Page__one-left-text">Маҳаллий ҳокимликлар чиқарган қарорга кўра, кеча, яъни 11 январдан кўпгина мактабларда ўқиш жараёни бошланди. Ташқаридаги ҳаво ҳарорати эса сўнгги йиллардаги айни шу мавсум кўрсаткичидан анча юқори. </p>
