import { createRouter, createWebHistory } from "vue-router";
import Section__one from "@/views/Section__one.vue";
import Section__twoo from "@/views/Section__twoo.vue";
import Section__thre from "@/views/Section__thre.vue";
import Section__for from "@/views/Section__for.vue";
import Section__five from "@/views/Section__five.vue";
import Section__six from '@/views/Section__six.vue';
import Page from '@/views/Page.vue';

const routers = createRouter({
    history:createWebHistory(),
    routes:[
        { path: "/one", name: "Section__one", component: Section__one},
        { path: "/twoo", name: "Section__twoo", component: Section__twoo},
        { path: "/thre", name: "ThrSection__three", component: Section__thre},
        { path: "/for", name: "Section__for", component: Section__for},
        { path: "/five", name: "Section__five", component: Section__five},
        { path: "/six", name: "Six", component: Section__six},
        { path: "/page", name: "Page", component: Page},
    ]
})
export default routers
